import {
    CheckCircle2,
    ChevronDown,
    ChevronRight,
    Circle,
    Download,
    Eye,
    EyeOff,
    Folder,
} from 'lucide-react';
import { useState } from 'react';
import { LayerGroup } from '../types';

interface LayerPanelProps {
    groups: LayerGroup[];
    toggleLayer: (groupId: string, layerId: string) => void;
    updateOpacity: (groupId: string, layerId: string, opacity: number) => void;
}

const LayerPanel: React.FC<LayerPanelProps> = ({
    groups,
    toggleLayer,
    updateOpacity,
}) => {
    const [expandedGroups, setExpandedGroups] = useState<
        Record<string, boolean>
    >({
        'group-basemap': false,
        'group-rtrw': true,
    });

    const toggleGroup = (id: string) => {
        setExpandedGroups((prev) => ({ ...prev, [id]: !prev[id] }));
    };

    return (
        <div className="h-full w-80 bg-slate-900/95 backdrop-blur-sm border-r border-slate-700 flex flex-col animate-in slide-in-from-left duration-300">
            <div className="p-5 border-b border-slate-700 bg-slate-900">
                <h2 className="text-lg font-bold text-primary-400 tracking-tight flex items-center gap-2">
                    <Folder size={20} />
                    DATA LAYERS
                </h2>
                <p className="text-xs text-slate-500 mt-1">
                    Manage visibility and opacity
                </p>
            </div>

            <div className="flex-1 overflow-y-auto p-4 space-y-4">
                {groups.map((group) => {
                    const isBaseMap = group.id === 'group-basemap';

                    return (
                        <div
                            key={group.id}
                            className="border border-slate-700 rounded-lg overflow-hidden bg-slate-800/50"
                        >
                            {/* Group Header */}
                            <button
                                onClick={() => toggleGroup(group.id)}
                                className="w-full flex items-center justify-between p-3 bg-slate-800 hover:bg-slate-750 transition-colors"
                            >
                                <span className="font-semibold text-sm text-slate-200">
                                    {group.name}
                                </span>
                                {expandedGroups[group.id] ? (
                                    <ChevronDown
                                        size={16}
                                        className="text-slate-400"
                                    />
                                ) : (
                                    <ChevronRight
                                        size={16}
                                        className="text-slate-400"
                                    />
                                )}
                            </button>

                            {/* Layer List */}
                            {expandedGroups[group.id] && (
                                <div className="p-3 space-y-3 bg-slate-900/30">
                                    {group.layers.map((layer) => (
                                        <div
                                            key={layer.id}
                                            className="space-y-2"
                                        >
                                            <div className="flex items-center justify-between">
                                                <div className="flex items-center gap-3">
                                                    <button
                                                        onClick={() =>
                                                            toggleLayer(
                                                                group.id,
                                                                layer.id
                                                            )
                                                        }
                                                        className={`transition-colors ${
                                                            layer.visible
                                                                ? 'text-primary-500'
                                                                : 'text-slate-600'
                                                        }`}
                                                        title={
                                                            isBaseMap
                                                                ? 'Select Base Map'
                                                                : 'Toggle Visibility'
                                                        }
                                                    >
                                                        {isBaseMap ? (
                                                            // Radio button style for base maps
                                                            layer.visible ? (
                                                                <CheckCircle2
                                                                    size={18}
                                                                />
                                                            ) : (
                                                                <Circle
                                                                    size={18}
                                                                />
                                                            )
                                                        ) : // Checkbox/Eye style for standard layers
                                                        layer.visible ? (
                                                            <Eye size={18} />
                                                        ) : (
                                                            <EyeOff size={18} />
                                                        )}
                                                    </button>
                                                    <span
                                                        className={`text-sm ${
                                                            layer.visible
                                                                ? 'text-slate-200'
                                                                : 'text-slate-500'
                                                        }`}
                                                    >
                                                        {layer.name}
                                                    </span>
                                                </div>

                                                {/* Color indicator for vector layers */}
                                                {layer.type === 'vector' &&
                                                    layer.color && (
                                                        <div
                                                            className="w-3 h-3 rounded-full shadow-sm"
                                                            style={{
                                                                backgroundColor:
                                                                    layer.color,
                                                            }}
                                                        />
                                                    )}

                                                {/* Download Button */}
                                                {layer.apiKey && (
                                                    <a
                                                        href={`/api/export/${layer.apiKey}`}
                                                        download
                                                        className="p-1.5 text-slate-500 hover:text-primary-400 hover:bg-slate-800 rounded transition-colors ml-1"
                                                        title="Download GeoJSON"
                                                    >
                                                        <Download size={16} />
                                                    </a>
                                                )}
                                            </div>

                                            {/* Opacity Slider - Only visible if layer is active */}
                                            {layer.visible && (
                                                <div className="flex items-center gap-2 pl-8 pr-1">
                                                    <span className="text-[10px] text-slate-500 uppercase">
                                                        Op
                                                    </span>
                                                    <input
                                                        type="range"
                                                        min="0"
                                                        max="1"
                                                        step="0.1"
                                                        value={layer.opacity}
                                                        onChange={(e) =>
                                                            updateOpacity(
                                                                group.id,
                                                                layer.id,
                                                                parseFloat(
                                                                    e.target
                                                                        .value
                                                                )
                                                            )
                                                        }
                                                        className="w-full h-1 bg-slate-700 rounded-lg appearance-none cursor-pointer accent-primary-500 hover:accent-primary-400"
                                                    />
                                                </div>
                                            )}
                                        </div>
                                    ))}
                                </div>
                            )}
                        </div>
                    );
                })}
            </div>
        </div>
    );
};

export default LayerPanel;
